.breathingApp {
  height: 100%;
  position: absolute; 
  left: 0;
  width: 100%;
  overflow: hidden;
  transition: background-color 1s;
  text-align: center;
}

h1, h2 {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle {
  margin: 50px auto 10px auto;
  animation: pulse;
  animation-duration: 2s;
}
